<script>
export default {
  name: "BaseSearch",
  props: {
    value: {
      type: [String, Number],
      default: "",
    },
    placeholder: {
      type: String,
      default: "",
    },
  },
  computed: {
    getValue: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit("setValue", value);
      },
    },
  },
  methods: {
    onClick() {
      this.$emit("click");
    },
    onKeyup() {
      this.$emit("keyup");
    },
  },
};
